body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button {
  border-radius: 5px;
  font-size: 30px;
  padding: 20px;
  text-decoration: none;
}
.button:hover {
  cursor:pointer
}
.button:focus {
  outline:0;
}
.green.button {
  background: #00b77a;
  color: white;
}
.pink.button {
  background: #b200b9;
  color: white;
}

.Start {
  text-align: center;
}

.Start h1 {
  margin: 0;
}
h5 {
  margin-top: 0;
  text-transform: uppercase;
}

.PlayBtn {
  background: #00b77a;
  color: white;
  font-size: 30px;
  padding: 20px;
  text-decoration: none;
  border-radius:5px;
}

.Header {
  color: white;
  left: 0;
  position:fixed;
  top: 0;
  width: 100%;
  background: #282C35;
  z-index:4;
}
.Header h2 {
  display: inline-block;
  margin: 3vw 1vw;
  text-align: center;
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  background: black;
}

footer ul {
  padding: 0;
  margin: 0;
}

footer ul li {
  list-style: none;
  display: inline-block;
  width: 33.33%;
}

footer a {
  padding: 15px;
  color: #ddd;
  color: rgba(255,255,255,0.7);
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
  font-size: 16px;
  width: 70%;
  display: inline-block;
}
footer a:hover, footer a.active {
  color: white;
}
footer a.active {
  border-top: 3px solid green;
  background: #111;
}

.make-tip {
  width: 90%;
  margin: 0 auto;
  position: fixed;
  top: 20%;
  left: 5%;
}
#marketing-btn, #build-btn {
  width: 42vw;
  height: 42vw;
  max-width: 220px;
  max-height: 220px;
  border-radius: 50%;
  border: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 25px;
}
.animated {
  transition: all .2s ease;
  -webkit-transform: scale(2);
  transform: scale(2);
  opacity: 0;
}

#marketing-btn h1, #build-btn h1 {
  margin: 0;
}

#build-btn {
  position:fixed;
  left: 5%;
  bottom: 25%;
}

#marketing-btn {
  position:fixed;
  right: 5%;
  bottom:25%;
}

ul.upgrades {
  display: block;
  padding: 0;
  width: 90vw;
  margin: 20vw 0;
}
.upgrades li {
  background: #222;
  border-radius:10px;
  list-style: none;
  margin: 6vw 0;
  padding: 5vw;
}
.upgrades h3 {
  margin: 0;
}
.upgrades button {
  float: right;
  font-size: 20px;
  padding: 10px;
}
button[disabled] {
  opacity: 0.3;
}

ul.achievements {
  display: block;
  padding: 0;
  width: 98vw;
  margin: 20vw 0;
}
.achievements li {
  background: #222;
  border-radius:10px;
  list-style: none;
  margin: 2vw;
  padding: 5vw;
  display: inline-block;
  width: 35vw;
}
.achievements h3, .achievements p {
  margin: 0;
}
.achievements li.locked {
  opacity: 0.4;
}

.achievements h1 {
  margin: 0;
}
.achievements .emoji {
  font-size: 60px;
}

